document.addEventListener('DOMContentLoaded', function(){
    eventListeners(); 

})
function eventListeners() {
    const checkbtn = document.querySelectorAll('.bi-check2-circle');
    checkbtn.forEach(check  => {
        check.addEventListener('click', check_done)

    });

    // btn para mostrar o ocultar 
    const btn_show = document.querySelector('.btn-add')
    btn_show.addEventListener('click', show_hide)
    

    const btn_list = document.querySelector('.btn-list')
    btn_list.addEventListener('click', show_hide )

    const close_task = document.querySelector('.form-show')
    close_task.addEventListener('click', close_form)

    const close_list = document.querySelector('.form-show-list')
    close_list.addEventListener('click', close_form)
    
}


function check_done(e) {
    const element = e.target;
    const cart = e.path[5]
    element.classList.toggle('bi-circle');
    cart.classList.toggle('check')

}

function show_hide(e) {
    
    const div_form_task = document.querySelector('.form-show')
    const div_form_list = document.querySelector('.form-show-list')

    const btn_list = document.querySelector('.btn-list')
    const btn_show = document.querySelector('.btn-add')

    if (e.target == btn_list ) {
        div_form_list.classList.toggle('form-hide')
        
    } else if (e.target == btn_show) {
        div_form_task.classList.toggle('form-hide')
    }
}

function close_form(e) {
    const div_form_task = document.querySelector('.form-show')
    const div_form_list = document.querySelector('.form-show-list')

    if ( e.target == div_form_task ) {
        div_form_task.classList.add('form-hide') 

    } else if (e.target == div_form_list ){
        div_form_list.classList.add('form-hide') 
    }

    
}